"use client";
import { signIn } from "@/lib/auth";

const GoogleSignin = () => {
  return (
    <button
    onClick={() => signIn('google')}
      className="bg-slate-50 p-2 rounded flex gap-x-2 items-center"
      
    >
      <span>
        <img className="w-[20px]" src={"/google.png"} alt="" />
      </span>
      Signin with Google
    </button>
  );
};

export default GoogleSignin;
